<template>
    <!--订单内容-->
    <div class="_shopOrder d-flex justify-content-center align-items-start ">
        <div id="_Order-box" class="_Order-box card card-body px-0 py-1 py-md-2 mx-0 ">
            <!--订单选项栏-->
            <div class="d-flex justify-content-between py-1">
                <div class="txt-subtitle txt-subtitle-space  px-2 px-sm-3 " style="font-weight:bold" @click="$router.go(-1)">
                    <i class="iconfont icon-left" style="font-size:22px;color:#1f1f1f"></i>
                    <span>我的收藏</span>
                </div>
            </div>
            <!--订单商品栏-->
            <div class="content py-1 py-md-3" >

                <goods-item :allList = "list" :path="$route.fullPath">
                    <template v-slot:cancelSlot="item">
                        <span class="_myNum btn rounded rounded-pill px-2 py-1 " @click.stop.prevent="onChangeCollection(item.id)"
                             style="color: #E79989;border: 2px solid #E79989;font-size:0.7rem; font-weight:bold;">取消收藏</span>
                    </template>
                </goods-item>

               <div v-if="!list.length" class="d-flex flex-column justify-content-center align-items-center" style="height:400px;">
                    <img src="~assets/images/pic/bg-collect.png" alt="" class="img-responsive " style="max-width:400px;">
                    <span class="txt-subbody" style="color:#5D6A68;">您还没有收藏的商品</span>
                    <div class="txt-subbody py-2">
                        <span class="">没有找到商品？试试</span>
                        <span class="text-danger  px-1" @click="$router.push('/')">找一找商品</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import $ from 'jquery';
    import {ElLoading, ElMessage} from 'element-plus';
    import {getCollectList, changeCollection} from 'network/collect';
    import GoodsItem from 'components/content/goods/GoodsItem';

    import {reactive, ref, toRefs, onMounted} from 'vue';
    import {useRouter, useRoute} from "vue-router";


    export default {
        name: "collection",
        components: {
            GoodsItem,
        },
        setup() {
            /*List 组件通过 loading 和 finished 两个变量控制加载状态，
            当组件滚动到底部时，会触发 load 事件并将 loading 设置成 true。
            此时可以发起异步操作并更新数据，数据更新完毕后，将 loading
            设置成 false 即可。若数据已全部加载完毕，则直接将 finished 设
            置成 true 即可。*/
            const router = useRouter();
            const route = useRoute();
            const state = reactive({

                 loading: false,
                 finished: false,
                 refreshing: false,

                list: [],                 // 所有商品列表
                page: 1,
                totalPage: 0,
            });
            onMounted(() => {
                let loadingInstance = ElLoading.service({ fullscreen: false,text:'加载页面中。。。',target: '.content'});

                onRefresh();
                if(loadingInstance!==null){
                    loadingInstance.close();                // 页面加载中...
                    loadingInstance = null;
                }
            });

            const loadData = () => {
                let loadingInstance = ElLoading.service({ fullscreen: false,text:'加载页面中。。。',target: '.content'});

                getCollectList(state.page).then(res=>{
                    console.log('collection-data',res);        // 这里没有状态码返回
                    state.list = state.list.concat(res.data);
                    // state.loading = false;

                    state.totalPage = res.meta.pagination.total_pages;
                    if (state.page >= state.totalPage) {
                        state.finished = true;         // 加载数据完毕就结束下拉加载
                    }

                    /*if (res.data.length !== 0) {
                        res.data.forEach(item => {
                            if(Object.keys(item.goods).length>0){
                                state.allList.push(item.goods)
                            }
                        })
                    }*/
                }).catch(err=>{
                    console.log('collection-err',err);
                    ElMessage.error('获取收藏数据错误')
                });

                if(loadingInstance!==null){
                    loadingInstance.close();                // 页面加载中...
                    loadingInstance = null;
                }
            };
            const onLoad = () => {
                if (!state.refreshing && state.page < state.totalPage) {
                    state.page = state.page + 1;
                }

                if (state.refreshing) {
                    state.list = [];
                    state.allList = [];
                    state.refreshing = false;
                }
                loadData();

            };
            const onRefresh = () => {
                //是否处于加载状态，加载过程中不触发Load事件
                state.refreshing = true;
                //清空列表数据
                state.finished = false;
                //重新加载数据
                // 将loading设置为true，表示处于加载状态
                state.loading = true;
                state.page = 1;
                onLoad();
            };


            // 跳转订单详情页
            const goTo = (id) => {
                router.push({path: '/orderdetail', query: {id}});
            };


            //
            const onChangeCollection = (id)=>{
                changeCollection(id).then(res=>{
                    if(res.status === 204){
                        ElMessage.success('已经取消收藏');

                        state.list.forEach((item,index) => {       //
                            if( item.goods.id === id){
                                state.list.splice(index,1)
                            }
                        })
                    } else{
                        ElMessage.warning('注意：收藏商品需要登录');
                    }
                }).catch(err=>{
                    console.log('changeCollection-err',err);
                    ElMessage.error('收藏错误！请重试')
                });
            };

            return {...toRefs(state), loadData, onRefresh , goTo, onChangeCollection};
        }
    }
</script>
<style scoped lang="scss">
    ._shopOrder {
        min-height: 600px;

        #_Order-box {
            max-width: 1200px;
            min-height:580px;
        }

        ._tab-order li {
            width: 20%;
        }

        ._tab-order li span {
            border-bottom: 3px solid white;
        }

        ._tab-order li.active span {
            border-bottom: 3px solid red;
        }

        .content {
            position: relative;
            width:100%;
            min-height:400px;
            /*left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            margin:0 auto;*/
            ._shopItemBox{
                border:2px solid #BAF3EA;
                border-radius:10px;
            }
        }
    }

</style>